import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import { Alert, Badge, Button, Card, CardBody, CardFooter, CardHeader, Col, Row } from 'reactstrap';
import { useQuery } from '@tanstack/react-query';
import useAxios from '../../hooks/useAxios';

const Dashboard = () => {
  const [isFetching, setIsFetching] = useState(false);
  const [list, setList] = useState([]);

  const api = useAxios();

  const { isLoading, error, data } = useQuery({
    queryKey: ['statusper'],
    queryFn: () =>
      api.get(`vapi/perusahaan/status`).then((res) => {
        return res.data.data;
      }),
  });

  useEffect(() => {
    async function fetchList() {
      setIsFetching(true);
      await api
        .get('vapi/tender/list-tender')
        .then((res) => setList(res.data.data))
        .catch((err) => console.log(err));
      setIsFetching(false);
    }

    fetchList();
  }, []);

  return (
    <>
      {isLoading ? (
        'Loading...'
      ) : error ? (
        'Something went wrong.'
      ) : data?.status_verifikasi === 'terverifikasi' ? (
        <>
          <Row>
            <h4 className="mb-3 mt-3">Tender yang Diikuti</h4>
            <Col md="6" lg="6">
              <Card className="border rounded-4 overflow-hidden">
                <CardHeader className="border-bottom fw-bold">
                  Tender Terbatas Konstruksi Barrier Stockpile Sulful
                </CardHeader>
                <CardBody>
                  <table className="w-100">
                    <tbody>
                      <tr>
                        <td>Lokasi Pekerjaan</td>
                        <td>:</td>
                        <td>Pelabuhan Kuala Langsa</td>
                      </tr>
                      <tr>
                        <td>Waktu Pendaftaran</td>
                        <td>:</td>
                        <td>22-29 Oktober 2023</td>
                      </tr>
                      <tr>
                        <td>Tipe Penyedia</td>
                        <td>:</td>
                        <td>Jasa</td>
                      </tr>
                      <tr>
                        <td>Bidang Usaha</td>
                        <td>:</td>
                        <td>Konstruksi</td>
                      </tr>
                      <tr>
                        <td>HPS</td>
                        <td>:</td>
                        <td>Rp 220.000.000</td>
                      </tr>
                      <tr>
                        <td>NIB Penyedia</td>
                        <td>:</td>
                        <td>20123 | 32032 | 41923 | 12789</td>
                      </tr>
                      <tr>
                        <td>Sistem Kualifikasi</td>
                        <td>:</td>
                        <td>Pra Kualifikasi</td>
                      </tr>
                    </tbody>
                  </table>
                  <Badge className="bg-light-primary text-primary p-2 mt-2 fw-bold">
                    Tender Dalam Tahap Pelaksanaan
                  </Badge>
                </CardBody>
                <CardFooter className="border-top">
                  <div className="d-flex justify-content-end">
                    <div className="d-flex gap-2">
                      <Button size="sm" type="button" outline className="rounded-3">
                        Input Dokumen Tahap II
                      </Button>
                      <Button size="sm" type="button" outline className="rounded-3">
                        Upload Dokumen Sanggah
                      </Button>
                      <Button size="sm" type="button" outline className="rounded-3">
                        Dokumen
                      </Button>
                    </div>
                  </div>
                </CardFooter>
              </Card>
            </Col>
            <Col md="6" lg="6">
              <Card className="border rounded-4 overflow-hidden">
                <CardHeader className="border-bottom fw-bold">
                  Tender Terbatas Konstruksi Barrier Stockpile Sulful
                </CardHeader>
                <CardBody>
                  <table className="w-100">
                    <tbody>
                      <tr>
                        <td>Lokasi Pekerjaan</td>
                        <td>:</td>
                        <td>Pelabuhan Kuala Langsa</td>
                      </tr>
                      <tr>
                        <td>Waktu Pendaftaran</td>
                        <td>:</td>
                        <td>22-29 Oktober 2023</td>
                      </tr>
                      <tr>
                        <td>Tipe Penyedia</td>
                        <td>:</td>
                        <td>Jasa</td>
                      </tr>
                      <tr>
                        <td>Bidang Usaha</td>
                        <td>:</td>
                        <td>Konstruksi</td>
                      </tr>
                      <tr>
                        <td>HPS</td>
                        <td>:</td>
                        <td>Rp 220.000.000</td>
                      </tr>
                      <tr>
                        <td>NIB Penyedia</td>
                        <td>:</td>
                        <td>20123 | 32032 | 41923 | 12789</td>
                      </tr>
                      <tr>
                        <td>Sistem Kualifikasi</td>
                        <td>:</td>
                        <td>Pra Kualifikasi</td>
                      </tr>
                    </tbody>
                  </table>
                </CardBody>
                <CardFooter className="border-top">
                  <div className="d-flex justify-content-end">
                    <div className="d-flex gap-2">
                      <Button size="sm" type="button" outline className="rounded-3">
                        Lebih Lanjut
                      </Button>
                      <Link to="/tender/tender-slug/upload-document">
                        <Button size="sm" type="button" color="success" className="rounded-3">
                          Upload Dokumen
                        </Button>
                      </Link>
                    </div>
                  </div>
                </CardFooter>
              </Card>
            </Col>
          </Row>
          <Row>
            <h4 className="mb-3 mt-3">Tender Tersedia</h4>
            {isFetching
              ? 'Loading..'
              : list?.length > 0
              ? list?.map((l) => (
                  <Col md="6" lg="6" key={l.id_tender}>
                    <Card className="border rounded-4 overflow-hidden">
                      <CardHeader className="border-bottom fw-bold">{l.nama_tender}</CardHeader>
                      <CardBody>
                        <table className="w-100">
                          <tbody>
                            <tr>
                              <td>Lokasi Pekerjaan</td>
                              <td>:</td>
                              <td>Pelabuhan Kuala Langsa</td>
                            </tr>
                            <tr>
                              <td>Waktu Pendaftaran</td>
                              <td>:</td>
                              <td>22-29 Oktober 2023</td>
                            </tr>
                            <tr>
                              <td>Tipe Penyedia</td>
                              <td>:</td>
                              <td>Jasa</td>
                            </tr>
                            <tr>
                              <td>Bidang Usaha</td>
                              <td>:</td>
                              <td>Konstruksi</td>
                            </tr>
                            <tr>
                              <td>HPS</td>
                              <td>:</td>
                              <td>Rp 220.000.000</td>
                            </tr>
                            <tr>
                              <td>NIB Penyedia</td>
                              <td>:</td>
                              <td>20123 | 32032 | 41923 | 12789</td>
                            </tr>
                            <tr>
                              <td>Sistem Kualifikasi</td>
                              <td>:</td>
                              <td>Pra Kualifikasi</td>
                            </tr>
                          </tbody>
                        </table>
                      </CardBody>
                      <CardFooter className="border-top">
                        <div className="d-flex justify-content-end">
                          <div className="d-flex gap-2">
                            <Link to={`/tender/${l.slug}`}>
                              <Button size="sm" type="button" outline className="rounded-3">
                                Lebih lanjut
                              </Button>
                            </Link>
                            <Button size="sm" type="button" color="success" className="rounded-3">
                              Daftar
                            </Button>
                          </div>
                        </div>
                      </CardFooter>
                    </Card>
                  </Col>
                ))
              : 'No data available.'}
          </Row>
        </>
      ) : data?.status_verifikasi === 'review_submit' ? (
        <>
          {/* <Alert className="rounded-4" color="danger">
            Akun Anda belum terverifikasi! Cek komentar PVD secara berkala!
          </Alert> */}
          <Alert className="rounded-4 text-info" color="info">
            <div className="d-flex justify-content-between align-items-center">
              Data Perusahaan Anda dalam Tahap Review.
            </div>
          </Alert>
        </>
      ) : (
        <>
          <Alert className="rounded-4 text-danger" color="danger">
            <div className="d-flex justify-content-between align-items-center">
              Anda Belum Mengisi Data Perusahaan, Silakan lengkapi data perusahaan Anda.
              <Link to="/registration">
                <Button type="button" outline>
                  Selengkapnya
                </Button>
              </Link>
            </div>
          </Alert>
        </>
      )}
    </>
  );
};

export default Dashboard;
